.productUnit-frame-activity {
  position: absolute;
  display: flex;
  flex-direction: column;
  width:280px;
  height:365px;
  border: 1px solid darkgray;
  border-radius: 3px;
  transition: 0.1s ease-in-out;
  margin-right: 30px;
}

.productUnit-frame-activity:hover {
  border: 4px solid gray;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.18);
}

.productUnit-frame {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* width: 70%;
    /* height: 100%; */
    max-width:550px !important;
    max-height: 624px !important;
    border: 1px solid darkgray;
    border-radius: 3px;
    transition: 0.1s ease-in-out;
}

.productUnit-frame-wanner {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 90%;
  height: 90%;
  margin-right: 20px;
  border-radius: 3px;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.productUnit-frame-wanner-special {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 48%;
  height: 755px;
  margin-bottom: 40px;
  border-radius: 3px;
  transition: 0.3s ease-in-out;
}

.productUnit-frame:hover {
  border: 4px solid gray;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.18);
}

.productUnit-img-restaurant {
  height: 230px;
  border-bottom: 1px solid #ddd;
  background-image: url('assets/sub/main-restaurant15.jpg');
  background-repeat: no-repeat;
  background-size: cover; 
}

.productUnit-img {
    height: 230px;
    border-bottom: 1px solid #ddd;
    background-image: url('assets/main/activity/main-activity-test.png');
    background-repeat: no-repeat;
    background-size: cover; 
}

.productUnit-img-map {
  height: 230px;
  border-bottom: 1px solid #ddd;
}

.productUnit-img-wanner-special {
  border-radius: 20px;
  height: 110%;
  width: 100%;
  border-bottom: 1px solid #ddd;
  overflow:hidden;
}

.main-blog-img-frame {
  border-radius: 20px;
  height: 50%;
  overflow:hidden;
  transition: 0.3s ease-in-out;
}

.productUnit-img-wanner {
  border-radius: 20px;
  height: 480px;
  border-bottom: 1px solid #ddd;
  background-image: url('assets/main/activity/main-activity-test.png');
  background-repeat: no-repeat;
  background-size: cover; 
  margin-bottom:25px;
}

.productUnit-img-wanner:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.18);
}

.main-blog-img-frame:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4);
}

.productUnit-img-wanner-special:hover {
  transform: scale(1.5);
}

.productUnit-contents-wanner-special {
  margin-top: 20px;
}

.list-item-ic-cart {
    position: absolute;
    width: 67px;
    height: 67px;
    background: #D9D9D9;
    border-radius: 50%;
    transform: scale(0.7);
  }
  
  .list-item-ic-cart:hover {
    background: #5C7A89;  
  }

  .ic-cart {
    background: url('assets/css_sprites_sub.png') no-repeat -186px -545px;
      width: 56px;
      height: 78px;
    margin: 0 auto;
    transform: scale(0.4);
  }


.productUnit-map {
    overflow: hidden;
    height: 480px;
    border-bottom: 1px solid #ddd;
}

.productUnit-contents {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding:15px;
}

.productUnit-contents-like-activity {
  display: flex;
  align-items: center;
  float: right;
  margin-left: 100px;
}

.productUnit-contents-like-activity h1 {
  margin-left: -5px;
}


.productUnit-contents-bottom {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;

}

.unit-tit.text-style-10 {
    color: black;
    font-size: 10px;
    line-height: 16px;
    letter-spacing: -0.01em; 
  }
  
.unit-tit.text-style-13 {
    color: black;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: -0.01em;
    margin-bottom: 2px;
  }
  
.unit-tit.text-style-24 {
    color: black;
    font-weight: 500;
    font-size: 24px;
    line-height: 34px;
    letter-spacing: -0.01em;
    margin-bottom: 30px;
}

.unit-tit.price-20 {
    color: #000;
    font-size: 20px;
}

.unit-tit.like-13 {
    color: #3f4150;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: -0.01em;
    margin-bottom: 2px;
    vertical-align: baseline;
}

.productUnit-contents-like {
    display: flex;
    align-items: center;
    float: left;
    margin-top: -30px;
}

.productUnit-contents-like div {
  margin-left: -12px;
}

.productUnit-contents-like h1 {
  margin-left: -8px;
  font-weight: 800;
}

.star-rating-13, .star-rating-16 {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
    .star-rating-13 .ic-star, .star-rating-16 .ic-start {
      color: #e0e2e7; }
      .star-rating-13 .ic-star.is-active, .star-rating-16 .ic-star.is-active {
        color: #4e6a75; }
  
  .star-rating-13 {
    font-size: 13px; }
  
  .star-rating-16 {
    font-size: 16px; 
}

.blog-page-avatar {
  float: left;
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 10px;
}